import { createGlobalStyle } from 'styled-components'
import mainPattern from '../assets/backgrounds/mainPattern.png'

export default createGlobalStyle`
    body {
        
        background-size: 100vw;
        background-repeat: infinite;
        background-blend-mode: overlay;
        background-attachment: fixed;
        background-image:
            url(${mainPattern}),
            linear-gradient(
                ${(props) => props.theme.colors.gradient_1} ${(props) =>
    props.theme.gradient_stops.one}%,
                ${(props) => props.theme.colors.gradient_2} ${(props) =>
    props.theme.gradient_stops.two}%,
                ${(props) => props.theme.colors.gradient_3} ${(props) =>
    props.theme.gradient_stops.three}%,
                ${(props) => props.theme.colors.gradient_4} ${(props) =>
    props.theme.gradient_stops.four}%
            );
    }
    
    * {
        font-family: ${(props) => props.theme.fonts.regularTextFont}, sans-serif;
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        outline: 0;
    }    

    p {
        color: ${(props) => props.theme.colors.mainText}
    }

    .title {
        font-family: ${(props) => props.theme.fonts.titleFont};
        font-size: 1.5rem;
        color: ${(props) => props.theme.colors.mainText}
    }

    .section {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-width: 100vw;
    }   
   
    .separator {
        background-color: black;
        height: 10px;
        width: 100vw;
    }
`
