import React from 'react'
import { Bio } from './style'

const Biography = () => {
    return (
        <Bio className="section">
            <div className="container">
                <h2 className="title">Biografia</h2>
                <div className="textbox">
                    <p className="text">
                    Cria da BXD, DJ Sandinho  conhecido por ser o Alquimista das Aceleradas. 
                    Unindo o mais avanado ritmo do atabaque carioca com breakbeats clssicos de rock e soul,
                    suas msicas e apresentaes deixam evidente seu amor pela msica e pelos diferentes tipos de tambores.
                    <br></br>
                    Suas apresentaes de funk so sempre marcantes, comeando com funks mais pesados e acelerados do Rio de Janeiro, 
                    mas tambm transita entre ritmos mais leves e meldicos (como os funks de 2019) ou mais atuais, como funks do tiktok, 
                    pra formar uma apresentao com repertrio amplo e transies limpas, sem pausas.
                    Assim, Sandinho se estabeleceu como figura presente nos eventos universitrios da Rural, 
                    j tendo participado em eventos com DJ Julia Bacellar, Bonde das Maravilhas e DJ Sexy Love.
                    <br></br>
                    O DJ tambm  ativo na cena underground de msica eletrnica brasileira, 
                    atravs do selo independente ACABOU O SOSSEGO, estabelecido por ele e outros artistas. 
                    O selo conta hoje com 3 lanamentos entre o jungle/drum n bass e o funk, e conta com 
                    artistas de vrios estados do Brasil, alm de uma radio no YouTube focada em drum n bass.
                    <br></br>
                    Seu lanamento mais recente, AQUECIMENTO DA ALQUIMIA conta com mais de 300 streamings no Spotify,
                    e seu segundo single, REI DO WHISKY com MC Negritin,  est planejado para Agosto de 2023.
                    <br></br>
                    Escute AGORA a prvia da nova msica:
                    </p>
                </div>
            </div>
        </Bio>
    )
}

export default Biography
