import styled from 'styled-components'

export const Container = styled.section`
    padding: 0;
    background: black;
`

export const Box = styled.div`
    * {
        display: inline-block;
        position: relative;
    }

    img {
        display: block;
    }

    :after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        background: none;
        position: absolute;
    }
`

export const ConfiguredImage = styled.img`
    object-fit: contain;
`
