import styled from 'styled-components'

export const Bio = styled.main`
    * {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 1rem;
        font-size: 1.2rem;
        font-family: ${(props) => props.theme.fonts.regularTextFont}
    }

    .section {
        max-height: 70vh;
    }

    .container {
        max-width: 80%;
    }

    .title {
        font-family: ${(props) => props.theme.fonts.titleFont};
        font-size: 2.5rem;
        color: ${(props) => props.theme.colors.mainText}
    }

    .text {
        text-align: justify;
        margin: 1rem 1rem ;
        color: ${(props) => props.theme.colors.mainText};
    }

    .textbox {
        border-radius: .6rem;
        background: rgba(0, 0, 0, 80%)
    }

`
