import React from 'react'
import GlobalStyle from './styles/global.js'
import Home from './components/home'
import Biography from './components/biography'
import Theme from './Theme.jsx'
import './App.css'
import Gallery from './components/gallery/index.js'
import Previews from './components/previews/index.js'
import PromotionalSection from './components/promotional/index.js'

function App() {
    return (
        <>
            <Theme>
                <GlobalStyle />
                <div className="App">
                    <Home />
                    <Biography />
                    <Previews />
                    <Gallery />
                    <PromotionalSection />
                </div>
            </Theme>
        </>
    )
}

export default App
