import styled from 'styled-components'

export const Promotional = styled.div`
    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 50vh;
        margin: 2rem;


        .title {
            padding: 1rem 0;
        }

        p {
            padding: 1rem 0;
        }

        a {
            height: 50%;
            width: 50%;
            color: #fff;
        }

        .driveIcon {
            color: ${(props) => props.theme.colors.highlight};
            height: 50%;
            width: 60%;
        }
    }
`
