import React from 'react'
import PropTypes from 'prop-types'
import Carousel from 'react-material-ui-carousel'
import photo0 from '../../assets/artist/gallery/0.jpg'
import photo1 from '../../assets/artist/gallery/1.jpg'
import photo2 from '../../assets/artist/gallery/2.jpg'
import photo3 from '../../assets/artist/gallery/3.jpg'
import photo4 from '../../assets/artist/gallery/4.jpg'
import photo5 from '../../assets/artist/gallery/5.jpg'
import { Box, Container } from './style'
import { ConfiguredImage } from './style'

const items = [
    {
        src: photo0,
        width: '100%',
        height: '300px',
    },
    {
        src: photo1,
        width: '100%',
        height: '300px',
    },
    {
        src: photo2,
        width: '100%',
        height: '300px',
    },
    {
        src: photo3,
        width: '100%',
        height: '300px',
    },
    {
        src: photo4,
        width: '100%',
        height: '300px',
    },
    {
        src: photo5,
        width: '100%',
        height: '300px',
    },
]

const Item = (props) => {
    return (
        <Container>
            <Box>
                <ConfiguredImage
                    src={props.item.src}
                    width={props.item.width}
                    height={props.item.height}
                />
            </Box>
        </Container>
    )
}

const Gallery = () => {
    return (
        <Carousel>
            {items.map((item, i) => (
                <Item key={i} item={item} />
            ))}
        </Carousel>
    )
}

Item.propTypes = {
    item: PropTypes.object.isRequired,
}

export default Gallery
