import React from 'react'
import { HomeStyle } from './style'
import ArtistFront from '../../assets/artist/front.png'
import ArtistLogo from '../../assets/artist/logo.png'
import ArtistLogo2 from '../../assets/artist/logo2.png'

const Home = () => {
    return (
        <>
            <HomeStyle className="section">
                <img src={ArtistFront} className="img-section" />
                <img src={ArtistLogo} className="logo-section" />
                <img src={ArtistLogo2} className="logo-section2" />
            </HomeStyle>
        </>
    )
}

export default Home
