import React from 'react'
import { previewUrl1, previewUrl2, previewUrl3 } from '../../constants/previewUrl'
import { Previews } from './style'

const PreviewSection = () => {
    return (
        <Previews className="section">
        
        <h2 className="header">Escute AGORA mesmo a prvia do prximo lanamento, REI DO WHISKY:</h2>
        
            <div className="playerContainer">
                <iframe
                    src={previewUrl1}
                    title="YouTube video player"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                    allowfullscreen
                ></iframe>
            </div>
        <h2 className="header">E os lanamentos anteriores, no Spotify e YouTube:</h2>
        
            <div className="playerContainer">
                <iframe className="spotify"
                        allow="autoplay"
                        src={previewUrl2}
                    >
                </iframe>
                <iframe className="spotify"
                    allow="autoplay"
                    src={previewUrl3}
                ></iframe>                
            </div>
        </Previews>
    )
}

export default PreviewSection
