import React from 'react'
import { Promotional } from './style'
import { FolderOpenRounded } from '@mui/icons-material'
import driveLink from '../../constants/driveLink'

const PromotionalSection = () => {
    return (
            <Promotional>
                <div className="container">
                    <h2 className="title">Fotos e logo para divulgao</h2>
                    
                    <p>Clique no cone para acessar a logo e fotos promocionais</p>

                    <a target="blank" href={driveLink}>
                        <FolderOpenRounded className="driveIcon" />
                    </a>
                </div>
            </Promotional>
    )
}

export default PromotionalSection
