import styled from 'styled-components'

export const HomeStyle = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    max-height: 100vh;
    height: 100%;

    .img-section {
        object-fit: cover;
        max-width: 94vw;
        height: 60vh;
    }

    @keyframes spawnLogo {
        0% {
            height: 0;
        }
        50% {
            height: 25vh;
        }
        100% {
            height: 15vh;
        }
    }

    @keyframes spinCircle {
        
        0% {
            transform:rotate(0deg);
        }
        50% {
            transform:rotate(180deg);
        }
        100% {
            transform:rotate(0deg);
        }
    }

    .logo-section {
        position: absolute;
        max-height: 25vh;
        height: 15vh;
        max-width: 90%;
        animation-name: spawnLogo;
        animation-duration: 3s;
        bottom: 5%;

        img {
            color:white;
        }
    }

    .logo-section2 {
        position: absolute;
        max-height: 50vh;
        /* height: 15vh; */
        animation-name: spinCircle;
        animation-duration: 10s;
        animation-iteration-count: infinite;
        top: -10%;
        bottom: -15%;
    }

`
