import styled from 'styled-components'

export const Previews = styled.div`
    * {
        display: flex;
        gap: 20px;
        flex-direction: column;
        max-width: 100%;
    }

    .header {
        color: ${(props) => props.theme.colors.highlight};
        margin: 1.5rem 1rem 1rem 1rem;
        padding: 1.5rem 1rem 1rem 1rem;
        width: 80%;
        border-radius: .6rem;
        background: rgba(0, 0, 0, 80%)
        }

    .playerContainer {
        width: 80%;
        padding: 3% 0px;

        iframe {
            outline: none;
            border-radius: 10px;
            border-style: hidden;
            box-shadow: 0 0 12px ${(props) => props.theme.colors.highlight};
        }
    }    
    .spotify {
        height: 152px;
    };
`
